.mainimg{
    width: 100%;
    height: 400px;
    background-color: rgb(60, 255, 76);
    margin-bottom: 10px;
}

.product_total_box{
    margin: 0 auto;
    width: 1120px;
    height: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.product_one_box{
    width: 270px;
    height: 400px;
    border: 1px solid rgb(231, 231, 231);
    box-sizing: border-box;
    padding: 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}

.product_one_box img{
    width: 192px;
    display: block;
}

.name{
    align-self: flex-start;
}

.price{
    align-self: flex-end;
}

.product_one_box span{
    font-size: 30px;
}