.info_area{
    width: 100%;
    height: 260px;
    background-color: #ccc;
}

.info_area_in{
    margin: 0 auto;
    width: 1180px;
    height: 260px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.info_area_in>li{
    width: 25%;
    height: 140px;
    border-right: 1px solid rgb(161, 161, 161);
    padding-left: 20px;
    box-sizing: border-box;
}

.info_area_in>li:first-child{
    padding-left: 0;
}

.info_area_in>li:last-child{
    flex-direction: row;
    border-right: 0;
}

.info_area_in>li>h5{
    font-size: 25px;
    font-weight: 900;
    margin-bottom: 10px;
}

.info_area_in>li>div{
    width: 100%;
    height: 100px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.info_area_in>li>div>p{
    font-size: 14px;
    color: rgb(119, 119, 119);
}

.info_area_in>li>div>span{
    font-size: 14px;
}

.info_area_in>li>div>h6{
    font-size: 25px;
}

.info_area_in .last_box{
    display: flex;
    width: 100%;
    height: 100%;
}

.info_area_in .last_box li{
    width: 50%;
    height: 100%;
    background-color: turquoise;
}

