.wrap{
  width: 100%;
}

.header{
  width: 100%;
  height: 100px;
}

.header_in{
  margin: 0 auto;
  width: 1180px;
  height: 100px;
}

.header_in_top{
  width: 100%;
  height: 40px;
}

.header_in_top>ul{
  float: right;
  width: 265px;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

/* Link 가 걸린 텍스트 글자색 바꾸는방법 찾아봐야함 */
.header_in_top>ul>li{
}

.header_in_bottom{
  width: 100%;
  height: 60px;
}


.logo{
  float: left;
  width: 150px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.nav{
  float: left;
  width: 750px;
  height: 60px;
}

.nav ul{
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.nav ul li{
  color: #555;
}

.nav_btn{
  float: right;
  width: 200px;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.nav_btn li{
  width: 25%;
  height: 100%;
  background-color: turquoise;
}










/* 메인페이지 */
.mainimg{
  width: 100%;
  height: 580px;
  background: url(./img/main1.png) no-repeat 50% 0;
  overflow: hidden;
}










/* con1 */
.con1{
  width: 100%;
  height: 610px;
}

.con1_in{
  margin: 0 auto;
  width: 1180px;
  height: 100%;
  padding-top: 100px;
  box-sizing: border-box;
}

.con1_text_box{
  float: left;
  width: 240px;
  height: 100%;
}

.con1_text_box h4{
  font-size: 30px;
  margin-bottom: 15px;
}

.con1_text_box p{
  font-size: 15px;
  line-height: 25px;
  color: #555;
}

.con1_list_box{
  float: right;
  width: 850px;
  height: 100%;
  display: flex;
  justify-content: space-between;
}

.con1_list_box_one{
  width: 270px;
  height: 410px;
  border: 1px solid rgb(231, 231, 231);
  box-sizing: border-box;
  padding: 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.con1_list_box_one img{
  width: 192px;
  display: block;
}

.con1_list_box_one_name{
  align-self: flex-start;
}

.con1_list_box_one_price{
  align-self: flex-end;
}

.con1_list_box_one_price span{
  font-size: 25px;
}


















/* con2 */
.con2{
  width: 100%;
  height: 1130px;
  background-color: rgb(243,245,247);
}

.con2_in{
  margin: 0 auto;
  width: 1180px;
  height: 100%;
  padding-top: 100px;
  box-sizing: border-box;
}

.con2_in h4{
  font-size: 35px;
  margin-bottom: 30px;
}

.con2_nav{
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 785px;
  height: 20px;
  margin-bottom: 30px;
}

.con2_nav li{
  color: #555;
}

.con2_nav li:first-child{
  color: black;
  font-weight: 900;
}

.con2_list_box{
  width: 100%;
  height: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.con2_list_box_one{
  float: left;
  width: 270px;
  height: 410px;
  border: 1px solid rgb(231, 231, 231);
  box-sizing: border-box;
  padding: 15px;
  padding-top: 50px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 20px;
  background-color: #fff;
}

.con2_list_box_one img{
  width: 192px;
  display: block;
}

.con2_list_box_one_name{
  align-self: flex-start;
}

.con2_list_box_one_price{
  align-self: flex-end;
}

.con2_list_box_one_price span{
  font-size: 25px;
}












