.mainimg{
    width: 100%;
    height: 400px;
    background-color: rgb(33, 73, 158);
}

.project_list{
    margin: 0 auto;
    margin-top: 60px;
    width: 1120px;
    height: 600px;
}

.pro_li_h{
    width: 100%;
    height: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.pro_li_b{
    width: 100%;
    height: 570px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}






/* 남양기획전 */

.item{
    float: left;
    width: 550px;
    height: 260px;
}

.img{
    width: 550px;
    height: 200px;
    overflow: hidden;
    border: 1px solid #ccc;
}

.item h5{
    font-size: 18px;
    font-weight: 400;
    margin-top: 24px;
}

.item p{
    margin-top: 9px;
    font-size: 15px;
    color: #555;
}








/* 종료된 이벤트 */
.company_project{
    margin: 0 auto;
    margin-top: 60px;
    width: 1120px;
    height: 600px;
}

.pro_li_h02{
    width: 100%;
    height: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.pro_li_b02{
    width: 100%;
    height: 570px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.end_event_box{
    width: 100%;
    height: 240px;
    border-bottom: 1px solid #ccc;
}

.end_event_box img{
    float: left;
    width: 360px;
    height: 180px;
    margin-bottom: 50px;
}

.middle_box{
    float: left;
    width: 500px;
    height: 240px;
}

.middle_box p{
    font-size: 22px;
    font-weight: 900;
}

.middle_box span{
    font-size: 14px;
}

